import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subject, takeUntil, Subscription, BehaviorSubject } from "rxjs";
import { NotificationServiceService } from "src/@core/Models/Notification/notification-service.service";
import { BranchService } from "src/app/erp-hr/data/configuration-services/branch.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";
import { ManageBranchesComponent } from "../../../configurations/branches/dialogs/manage-branches/manage-branches.component";
import { HttpParams } from "@angular/common/http";
import {
  FormGroup,
  FormBuilder,
  Validators,
 
} from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { TokenStorageService } from "src/app/core/service/token-storage.service";

import { JobManagementService } from "src/app/erp-hr/data/job-services/job-management.service";
import { DepartmentsLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/departments-lookup/departments-lookup.component";
import { JobRolesLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/job-roles-lookup/job-roles-lookup.component";

@Component({
  selector: "app-manage-jobs",
  templateUrl: "./manage-jobs.component.html",
  styleUrls: ["./manage-jobs.component.sass"],
})
export class ManageJobsComponent implements OnInit {
  showForm = false;
  isLoading = true;
  pageFunction = "Add";
  mngForm: FormGroup;
  currentUser: any;
  currentUserCode: any;
  formData: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  hideSubmit = false;
  hideApprovals: boolean = true;
  approvalVisible: boolean = false;
  disableActions = false;
  requestCode: any;

  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  applicationTypeArray: any[] = ["EXTERNAL-JOB", "INTERNAL-JOB", "OPEN-JOB"];
  jobTermArray: any = [
    "Permanent",
    "Contract",
    "Intern",
    "Attachment",
    "Secondment",
  ];
  jobLevelArray: any = [
    "Executive Position",
    "Lead Position",
    "Technical Engineer",
    "Technical Assistant",
    "Intermediate Position",
    "Entry Level",
    "Internship Level",
    "Attachment Level",
  ];
  jobShiftArray: any = ["Day Shift", "Night Shift"];
  jobRolesColumns: string[] = ["index", "description", "actions"];

  constructor(
    private fb: FormBuilder,
    private tokenStorageService: TokenStorageService,
    private snackbar: SnackbarService,
    private router: Router,
    private route: ActivatedRoute,
    private jobManagementService: JobManagementService,
    private dialog: MatDialog
  ) {
    this.currentUser = this.tokenStorageService.getUser().username;
    this.currentUserCode = this.tokenStorageService.getUser().empPfNo;
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  ngOnInit(): void {
    this.getPage();

    if (
      this.route.queryParams &&
      typeof this.route.queryParams.subscribe === "function"
    ) {
      this.route.queryParams.subscribe((params) => {
        console.log("params: ", params);
        if (params.requestCode) {
          this.requestCode = params.requestCode;
          this.getDataById();
          this.pageFunction = params.action;
        }
      });
      this.showForm = true;
    } else {
      this.showForm = true;
    }
  }

  getDataById() {
    const params = new HttpParams().set("id", this.requestCode);
    this.jobManagementService
      .readById(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.entity) {
            //in thsi res, I cant access anything beyond res.entity. Why???
            this.formData = res.entity;
            console.log("this.formData: ", this.formData);
            if (this.pageFunction === "View") {
              this.activateViewMode();
            }

            if (res && res.entity && res.entity.jobqualifications) {
              this.dataSource.data = res.entity.jobqualifications;
              this.refresh();
            }

            this.showForm = true;
            this.getPage();
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      });
  }

  getPage(): void {
    if (this.pageFunction === "Add") {
      this.generateForm();
      this.generateSubForm();
      this.hideApprovals = true;
    } else if (this.pageFunction === "Update") {
      this.generateForm(this.formData);
      this.generateSubForm();
      this.hideApprovals = true;
    } else if (this.pageFunction === "View") {
      this.generateForm(this.formData);
      this.generateSubForm();
      this.hideSubmit = true;
      this.hideApprovals = true;
    }
  }
  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }
  generateForm(formData?: any): void {
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      id: [formData?.id ?? ""],
      jobTitle: [formData?.jobTitle ?? "", Validators.required],
      deadlineDate: [formData?.deadlineDate ?? new Date(), Validators.required],
      location: [formData?.location ?? "", Validators.required],
      jobTerm: [formData?.jobTerm ?? "", Validators.required],
      jobLevel: [formData?.jobLevel ?? "", Validators.required],
      jobShift: [formData?.jobShift ?? "", Validators.required],
      vacantPositions: [formData?.vacantPositions ?? "", Validators.required],
      departmentId: [formData?.departmentId ?? "", Validators.required],
      jobGroupName: [formData?.jobGroupName ?? "", Validators.required],
      minSalary: [formData?.minSalary ?? "", Validators.required],
      //midSalary: [formData?.midSalary ?? "", Validators.required],
      maxSalary: [formData?.maxSalary ?? "", Validators.required],

      organizationRoleId: [
        formData?.organizationRoleId ?? "",
        Validators.required,
      ],

      applicationType: [formData?.applicationType ?? "", Validators.required],
      numberOfInterviews: [
        formData?.numberOfInterviews ?? "",
        Validators.required,
      ],
      whyJobIsNeeded: [formData?.whyJobIsNeeded ?? "", Validators.required],
      applicationProcess: [
        formData?.applicationProcess ?? "",
        Validators.required,
      ],
      jobDescription: [formData?.jobDescription ?? "", Validators.required],

      //jobRoles: [formData?.jobRoles ?? []],
      jobqualifications: [formData?.jobqualifications ?? []],

      //jobDuration
      //departmentId
      jdAttachment: [formData?.jdAttachment ?? ""],
    });
  }

  //*************************************************************************************************************************************** */
  //Lookups
  selectedItem: any;
  jobRoleLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "SingleEmployee",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(JobRolesLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      if (res && res.data && res.data.length > 0) {
        this.mngForm.patchValue({
          //midSalary: res.data[0].midSalary,
          minSalary: res.data[0].minSalary,
          maxSalary: res.data[0].maxSalary,
          organizationRoleId: res.data[0].id,
          jobGroupName: res.data[0].jobGroupName,
          jobLevel: res.data[0].roleName,
        });
      }
    });
  }
  departmentLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "SingleBDepartment",
      selected: "",
    };
    const dialogRef = this.dialog.open(
      DepartmentsLookupComponent,
      dialogConfig
    );
    dialogRef.afterClosed().subscribe((res) => {
      if (res && res.data && res.data.length > 0) {
        this.mngForm.patchValue({
          departmentId: res.data[0].id,
        });
      }
    });
  }

  // **************************************************************************************************
  //JobRequirements
  displayedColumns: string[] = ["id", "description", "action"];
  tranForm: FormGroup;
  showParTranForm = false;
  parTranAction: string = "";
  selectedParTranIndex: number;
  debitTotal: number = 0;
  creditTotal: number = 0;
  balance: number = 0;

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  generateSubForm(formData?): void {
    this.tranForm = this.fb.group({
      id: [formData?.id ?? ""],
      description: [formData?.narration ?? "", [Validators.required]],
    });
  }
  resetFormAndHide() {
    this.mngForm.patchValue({
      jobqualifications: this.dataSource.data,
    });
    this.tranForm.reset();
    this.showParTranForm = false;
  }

  addParTran() {
    this.parTranAction = "Add";
    this.showParTranForm = true;
  }

  refresh() {
    this.dataSource.data = [...this.dataSource.data];
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  editParTran(data: any, index: number) {
    this.parTranAction = "Update";
    this.selectedParTranIndex = index;
    this.generateSubForm(data);
    this.showParTranForm = true;
  }

  pushToDataSource() {
    if (this.parTranAction === "Add") {
      this.dataSource.data.push(this.tranForm.value);
    } else if (this.parTranAction === "Update") {
      let indexToUpdate: number;

      if (this.tranForm.value.id) {
        indexToUpdate = this.dataSource.data.findIndex(
          (item) => item.id === this.tranForm.value.id
        );
      } else {
        indexToUpdate = this.selectedParTranIndex;
      }

      if (indexToUpdate !== -1) {
        this.dataSource.data[indexToUpdate] = this.tranForm.value;
      }
    }

    this.resetFormAndHide();
    this.refresh();
  }

  cancelParTran() {
    this.resetFormAndHide();
  }

  deleteParTran(index: number) {
    this.dataSource.data.splice(index, 1);
    this.refresh();
  }

  // **************************************************************************************************
  //Showing the submit button
  posting: boolean = false;
  onSubmit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    if (this.pageFunction == "Add") {
      this.jobManagementService
        .create(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);

            if (res.statusCode == 201) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.cancel();
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
            this.posting = false;
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (this.pageFunction == "Update") {
      this.jobManagementService
        .update(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.cancel();
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
            this.posting = false;
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    }
  }

  cancel() {
    this.router.navigate(["/erp-hr/jobs/all-jobs"]);
  }

  // *********************************************************************************************

  // **********************************************************************************************
}
