import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { BranchService } from 'src/app/erp-hr/data/configuration-services/branch.service';
import { EmployeesLookupComponent } from 'src/app/erp-hr/hr-lookups/configurations-lookups/employees-lookup/employees-lookup.component';
import { FilesService } from 'src/app/shared/services/files.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { AllEmployeesComponent } from '../../../employees/all-employees/all-employees.component';
import { JobInterviewAPIService } from 'src/app/erp-hr/data/job-services/job-interviews.service';

@Component({
  selector: 'app-manage-job-interview-results',
  templateUrl: './manage-job-interview-results.component.html',
  styleUrls: ['./manage-job-interview-results.component.sass']
})
export class ManageJobInterviewResultsComponent implements OnInit {

  mngForm: FormGroup;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;
  isLoading: boolean = true;

  constructor(
    public dialogRef: MatDialogRef<AllEmployeesComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private jobInterviewAPIService: JobInterviewAPIService,
    private dialog: MatDialog,
    private filesService: FilesService
  ) { }

  ngOnInit(): void {
    console.log("LOG DATA = ", this.data);
    this.pageFunction = this.data.action;
    this.formData = this.data.record
    this.generateForm(this.formData);

    this.getPage();
  }
  ngAfterViewInit() {
    if (this.data.action !== "Add") {
      this.generateForm(this.formData);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  disableTransactionType: boolean = true;
  getPage() {
    this.disableTransactionType = true;
    console.log("this.data.action :", this.data.action);
    if (this.data.action === "Add") {
      this.isLoading = false;
      this.generateForm(this.data.record);
    } else if (this.data.action === "Update") {
      this.generateForm(this.data.record);
    } else if (this.data.action === "View") {
      this.generateForm(this.data.record);
      this.activateViewMode();
    }
    this.isLoading = false;
  }

  formData: any;

  generateForm(formData?): void {
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      id: [formData?.id ?? ''],
      overallImpression: [formData?.overallImpression ?? '', Validators.required],
      behavioralCompetency: [formData?.behavioralCompetency ?? '', Validators.required],
      roleSpecific: [formData?.roleSpecific ?? '', Validators.required],
      cultureFit: [formData?.cultureFit ?? '', Validators.required],
      hiringManagerRecommendation: [formData?.hiringManagerRecommendation ?? '', Validators.required],
      technicalScore: [formData?.technicalScore ?? 0, Validators.required],
      experienceScore: [formData?.experienceScore ?? 0, Validators.required],
      academicScore: [formData?.academicScore ?? 0, Validators.required],
      overallScore: [formData?.overallScore ?? 0, Validators.required],
      status: [formData?.status ?? '', Validators.required]
    });
  }



  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }

  posting: boolean = false;

  response: any;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    this.jobInterviewAPIService.interviewResults(this.mngForm.value)
    .pipe(takeUntil(this.destroy$))
    .subscribe({
      next: (res) => {
        this.response = res;
        console.log("res: ", res);
        if (res.statusCode == 200) {
          this.snackbar.showNotification(
            "snackbar-success",
            res.message
          );
        } else {
          this.snackbar.showNotification(
            "snackbar-danger",
            res.message
          );
        }
      },
      error: (err) => {
        this.snackbar.showNotification("snackbar-danger", err.message);
      },
      complete: () => {
        this.posting = false;
        setTimeout(() => {
          this.onNoClick();
        }, 3000);
      },
    }),
    Subscription;
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }



}
