import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ManageJobsComponent } from "./job-details/manage-jobs/manage-jobs.component";
import { RoutePrivilegeGuard } from "src/app/erp-procurement/data/services/_AccessControlAuthGuard.service";
import { ManageJobApplicationsComponent } from "./job-applications/manage-job-applications/manage-job-applications.component";
import { ManageJobInterviewsComponent } from "./job-interviews/manage-job-interviews/manage-job-interviews.component";
import { AllJobsComponent } from "./job-details/all-jobs/all-jobs.component";
import { AllJobApplicationsComponent } from "./job-applications/all-job-applications/all-job-applications.component";
import { AllJobInterviewsComponent } from "./job-interviews/all-job-interviews/all-job-interviews.component";

const routes: Routes = [
  {
    path: "all-jobs",
    component: AllJobsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "manage-jobs",
    component: ManageJobsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "all-job-applications",
    component: AllJobApplicationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "manage-job-applications",
    component: ManageJobApplicationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "all-job-interviews",
    component: AllJobInterviewsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "manage-job-interviews",
    component: ManageJobInterviewsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class JobsRoutingModule { }
