import { Component, Inject, OnInit, ViewChild } from "@angular/core";
import { AllJobApplicationsComponent } from "../../all-job-applications/all-job-applications.component";
import { FormBuilder } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { SelectionModel } from "@angular/cdk/collections";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subject, takeUntil } from "rxjs";
import { DocumentManagementSystemService } from "src/app/erp-hr/data/job-services/document-management-system.service";
import { HttpParams } from "@angular/common/http";

@Component({
  selector: "app-application-details",
  templateUrl: "./application-details.component.html",
  styleUrls: ["./application-details.component.sass"],
})
export class ApplicationDetailsComponent implements OnInit {
  Data: any;

  displayedColumns: string[] = ["index", "filename", "filetype", "action"];

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  destroy$: Subject<boolean> = new Subject<boolean>();

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  pageFunction: any = "Add";
  constructor(
    public dialogRef: MatDialogRef<AllJobApplicationsComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private dmsAPI: DocumentManagementSystemService
  ) {}

  ngOnInit(): void {
    this.pageFunction = this.data.action;
    this.Data = this.data.record;
    this.getData();
  }

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  isLoading: boolean = false;
  getData(page?: number, size?: number) {
    this.isLoading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    let params = new HttpParams().set(
      "referenceIdentifier",
      this.Data.emailAddress
    );
    console.log("this.params :", params);
    this.dmsAPI
      .getDocByIdentifier(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.res :", res);
          if (res.statusCode === 302) {
            this.data = res;
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data.entity);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
            this.isLoading = false;
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
          this.isLoading = false;
        },
        complete: () => {
          this.isLoading = false;
        },
      });
  }
  base64StringData: any;

  getDocById(id: any) {
    this.isLoading = true;
    this.dmsAPI
      .getApplicantDocsBase64ById(id)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.base64StringData = res.entity;
          console.log(
            "the fetched document in base 64  format  ",
            this.base64StringData
          );

          this.downloadDocument(this.base64StringData);

          this.isLoading = false;
        },
        error: (err) => {
          this.isLoading = false;
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      });
  }

  downloadDocument(fileToDownload: any) {
    let file = fileToDownload.file;
    let fileName = fileToDownload.filename;
    let fileType = fileToDownload.filetype;

    const source = `${file}`;
    const link = document.createElement("a");
    link.href = source;
    link.download = `${fileName}.${fileType}`;
    link.click();
  }

  submit(){
    
  }
}
